import React, {useEffect, useState} from 'react';
import "./Profile.css";
import API_URL from '../Services/API/APIs';
import { useNavigate } from 'react-router-dom';
import {Helmet} from "react-helmet";


function Profile ({onLogoutClick}){
  const [profile, setProfile] = useState({});
  const [loader, setLoader] = useState(false);
  const navigate = useNavigate();


  useEffect(() => {
    setLoader(true)
    var Token = localStorage.getItem("accessToken")
    fetch(`${API_URL}/users/profile/`, {method: "GET", 
    headers: {'Authorization': `Token ${Token}`, 
    "Content-Type": "application/json"
    }})
        .then((response) => response.json())
        .then((data) => {
          // console.log("profile data", data);
          setProfile(data.data);
          setLoader(false)
        })
        .catch((err) => {
          // console.log(err.message);
        });
        
  }, []);

  function loggedOut() {
    
    localStorage.removeItem("userLogged")
    localStorage.removeItem("accessToken")
    navigate("/home");
    
}

  if(loader) return "Loading...."
  // console.log(profile)
  return (
    <>
    <Helmet>
        <meta charSet="utf-8" />
        <title>Profile | The Teknology</title>
        <meta name="description" content="Profile Page of The Teknology." />
        <meta name="keywords" content="Profile page" />
    </Helmet>
    
    <div className='main-container'>
      <div className="profile-box profile">
        {/* <img src="profile-picture.jpg" alt="Profile Picture" /> */}
        <h2>{profile.name}</h2>
        <p>{profile.email}</p>
        {/* <ul class="social-links">
          <li><a href="/"><i class="fa fa-linkedin"></i></a></li>
          <li><a href="/"><i class="fa fa-github"></i></a></li>
          <li><a href="/"><i class="fa fa-twitter"></i></a></li>
        </ul> */}
        <button onClick={() => {loggedOut(); onLogoutClick();}}>Logout</button>
      </div>
    </div>



    </>
  )
  
}

export default Profile
