import './App.css';
import Navbar from './components/Navbar';
import {BrowserRouter as Router, Routes, Route} from 'react-router-dom';
import Blog from './components/Blogs/BlogPage/BlogPage';
import Home from  './components/Home/Home';
import ContactUs from './components/ContactUs/ContactUs';
import Register from './components/Auth/Register';
import BlogDetail from './components/Blogs/BlogDetail/BlogDetail';
import Login from './components/Auth/Login';
import React, {useEffect, useState} from 'react';
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import Profile from './components/Profile/Profile';
import Footer from './components/Footer';
import Error from './components/Error/Error';
toast.configure();


function App() { 
  const [isLoggedIn, setIsLoggedIn] = useState(false);

  function setLoginTrue() {
    // const ifLogin = localStorage.getItem('userLogged')
    // console.log("checkin if login", ifLogin)
    setIsLoggedIn(true);
  }

  function loggedOut(){
    // localStorage.removeItem("userLogged")
    // localStorage.removeItem("accessToken")
    setIsLoggedIn(false);
    // navigate("/home");
  }

  

  useEffect(() => {
    const ifLogin = localStorage.getItem('userLogged')
    // console.log("user logged", ifLogin)
    if (ifLogin) {
      setIsLoggedIn(true);
      // console.log("check", isLoggedIn)
    }
    else {
      // console.log("user not logged")
    }
    
  })


  return (
    <>
    <Router>
    <Navbar loggedIn={isLoggedIn}  />
     <Routes>
        <Route path='/' element={ <Home /> } />
        <Route path='/home' element={ <Home />} />
        <Route path='/blogs' element={ <Blog /> } />
        <Route path='/blogs/:slug' element={ <BlogDetail /> } />
        <Route path='/register' element={ <Register />}/>
        <Route path='/login' element={<Login ifLoggedIn={isLoggedIn} onLoginTrue={setLoginTrue}/>}/>
        <Route path='/contact-us' element= { <ContactUs /> } />
        <Route path='/profile' element={<Profile onLogoutClick={loggedOut}/>} />
        <Route path='/*' element={ <Error /> } />
     </Routes>
     <Footer />

     {/* <div className="App">
      {
        currentForm === "login" ? <Login onFormSwitch={toggleForm} /> : <Register onFormSwitch={toggleForm} />
      }
    </div> */}
     
     </Router>
     </>
  );
}

export default App;


