import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import API_URL from "../Services/API/APIs";
import { toast } from "react-toastify";
import "./Login.css";
import { Helmet } from "react-helmet";

export const Login = ({ onLoginTrue }) => {
  // const userLogged = useRef(false)
  const [username, setUsername] = useState("");
  const [pass, setPass] = useState("");
  const navigate = useNavigate();
  const [status, setStatus] = useState("");

  const handleSubmit = (e) => {
    e.preventDefault();
    // console.log(username);
  };

  function handleClick() {
    navigate("/register");
  }

  async function handleLogin() {
    const response = await fetch(API_URL + "/users/login/", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        username: username,
        password: pass,
      }),
    });
    const data = await response.json();
    if (data.status === 200) {
      // console.log("this is data", data);
      setStatus(data.message);

      localStorage.setItem("accessToken", data.token);
      localStorage.setItem("userLogged", true);

      // console.log("access token check", localStorage.getItem("userLogged"));
      toast.success(data.message, {
        position: toast.POSITION.BOTTOM_RIGHT,
        autoClose: true,
      });
      navigate("/home");
      onLoginTrue();
    } else {
      console.log(data.error);
      toast.error(data.error, {
        position: toast.POSITION.BOTTOM_RIGHT,
        autoClose: true,
      });
      setStatus(data.error);
    }
  }

  return (
    // <div className="auth-form-container Auth">
    <>
      <Helmet>
        <meta charSet="utf-8" />
        <title>Login | The Teknology</title>
        <meta name="description" content="Login Page of The Teknology." />
        <meta name="keywords" content="Login page" />
      </Helmet>
      {/* {!userLogged? ( */}
      <div className="auth-form-container Auth">
          <h2>Login</h2>
          <form className="login-form" onSubmit={handleSubmit}>
            <label htmlFor="username">username</label>
            <input
              value={username}
              onChange={(e) => setUsername(e.target.value)}
              type="username"
              placeholder="your username"
              //   id="email"
              name="username"
            />
            <label htmlFor="password">password</label>
            <input
              value={pass}
              onChange={(e) => setPass(e.target.value)}
              type="password"
              placeholder="********"
              id="password"
              name="password"
            />

            <button type="submit" onClick={handleLogin}>
              Log In
            </button>
            {<p>{status}</p>}
          </form>
          <button className="link-btn" onClick={handleClick}>
            Don't have an account? Register here.
          </button>
      </div>
      {/* }; */}
    </>
  );
};
export default Login;
