import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import API_URL from "../Services/API/APIs";
import { toast } from "react-toastify";
import "./Register.css";
import {Helmet} from "react-helmet";

export const Register = () => {
  const [email, setEmail] = useState("");
  const [pass, setPass] = useState("");
  const [name, setName] = useState("");
  const [status, setStatus] = useState();
  const navigate = useNavigate();

  function handleClick() {
    navigate("/login");
  }

  function handleSubmit(e) {
    e.preventDefault();
    console.log(email);

    fetch(API_URL + "/users/register/", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        email: email,
        password: pass,
        name: name,
      }),
    })
      .then((response) => response.json())
      .then((data) => {
        if (data.status === 200) {
          console.log("this is data", data);
          setStatus(data.message);

          toast.success(data.message, {
            position: toast.POSITION.TOP_RIGHT,
            autoClose: true,
          });
          // storing access token in local storage
          // localStorage.setItem("accessToken", data.token);
          // console.log("access token check", localStorage.getItem("accessToken"))
          navigate("/login");
          // loop for success and failure comes here
        } else {
          console.log(data.error);
          toast.error(data.error, {
            position: toast.POSITION.TOP_RIGHT,
            autoClose: true,
          });
          setStatus(data.error);
        }
      });
  }

  return (
    <>
      <Helmet>
        <meta charSet="utf-8" />
        <title>Sign Up | The Teknology</title>
        <meta name="description" content="Sign Up Page of The Teknology." />
        <meta name="keywords" content="Sign Up page" />
      </Helmet>
      <div className="auth-form-container Auth">
        <h2>Register</h2>
        <form className="register-form" onSubmit={handleSubmit}>
          <label htmlFor="name">Full name</label>
          <input
            value={name}
            name="name"
            onChange={(e) => setName(e.target.value)}
            id="name"
            placeholder="full Name"
          />
          <label htmlFor="email">email</label>
          <input
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            type="email"
            placeholder="youremail@gmail.com"
            id="email"
            name="email"
          />
          <label htmlFor="password">password</label>
          <input
            value={pass}
            onChange={(e) => setPass(e.target.value)}
            type="password"
            placeholder="********"
            id="password"
            name="password"
          />
          <button type="submit">Register</button>
        </form>
        {<p>{status}</p>}
        <button className="link-btn" onClick={handleClick}>
          Already have an account? Login here.
        </button>
      </div>
    </>
  );
};

export default Register;
