import React, {useEffect, useState} from 'react';
import { Link } from 'react-router-dom';
import './Navbar.css';
// import {Button} from './Button';

const STYLES = ['btn--primary', 'btn--outline']

const SIZES = ['btn--medium', 'btn--large']

const Button = (props, {
  children,
  type,
  onClick,
  buttonStyle,
  buttonSize,
}) => {
  const checkButtonStyle = STYLES.includes(buttonStyle) ? buttonStyle : STYLES[0];

  const checkButtonSize = SIZES.includes(buttonSize) ? buttonSize : SIZES[0]
  const loggedIn = props.data
  // console.log("user logged info", loggedIn)
  return (
      <>
      {loggedIn? (<Link to='/profile' className='btn-mobile'>
          <button
          className={`btn ${checkButtonStyle} ${checkButtonSize}`}
          onClick={onClick}
          type={type}
          >
          Profile
          </button>
      </Link>): (<Link to='/register' className='btn-mobile'>
          <button
          className={`btn ${checkButtonStyle} ${checkButtonSize}`}
          onClick={onClick}
          type={type}
          >
              Sign Up
          </button>
      </Link>)}
      
      </>
  )
};



function Navbar({loggedIn}) {
  const [click, setClick] = useState(false);
  const [button, setButton] = useState(true);
  // const [isLoggedIn, setIsLoggedIn] = useState(false);


  const handleClick = () => setClick(!click);
  const closeMobileMenu = () => setClick(false);
  // console.log("nav bar login check", loggedIn)

  const showButton = () => {
    if (window.innerWidth <= 960) {
      setButton(false);
    } else {
      setButton(true);
    }
  };

// useEffect(() => {
//     const ifLogin = localStorage.getItem('accessToken')
//     // console.log("user logged", ifLogin)
//     if (ifLogin) {
//       setIsLoggedIn(true);
//       // console.log("check", isLoggedIn)
//     }
//     else {
//       // console.log("user not logged in", isLoggedIn)
//     }
    
//   })

  useEffect(() => {
    showButton()
  }, [])

  window.addEventListener('resize', showButton);
  
  return (
    <> 
        <nav className="navbar container">
            <div className="navbar-container">
                <Link to='/' className="navbar-logo" onClick={closeMobileMenu}>
                    The Teknology 
                    {/* <i className="fab fa-typo3"/> */}
                </Link>
                <div className="menu-icon" onClick={handleClick}>
                  <i className={click ? 'fas fa-times': 'fas fa-bars'} />
                </div>
                <ul className={click ? 'nav-menu active': 'nav-menu'}>
                  <li className='nav-item'>
                    <Link to='/home' className='nav-links' onClick={closeMobileMenu}>
                      Home
                    </Link>
                  </li>
                  <li className='nav-item'>
                    <Link to='/blogs' className='nav-links' onClick={closeMobileMenu}>
                      Blogs
                    </Link>
                  </li>
                  <li className='nav-item'>
                    <Link to='/contact-us' className='nav-links' onClick={closeMobileMenu}>
                      Contact Us
                    </Link>
                  </li>

                  {/* {userLogged ? (<li className='nav-item'>
                    <Link to='/' className='nav-links-mobile' onClick={closeMobileMenu}>
                      LoggedIn 
                    </Link>
                  </li>) : (
                    <Link to='/register' className='nav-links-mobile' onClick={closeMobileMenu}>
                      Sign Up
                  </Link>
                  )} */}
                  
                </ul>
                {/* {onLogin? (<Link to='/profile' className='btn-mobile'>
                  <button onClick='/profile' >Profile</button>
                  </Link>) : (<Link to='/login' className='btn-mobile'>
                    <button onClick={onLogin}>Log in</button>
                    </Link>)} */}

                {loggedIn ? (button && <Button data={loggedIn} />) : (
                  button && <Button data={loggedIn}  />
                )}
            </div>
        </nav>       
    </>
  )
}

export default Navbar
