import React from 'react'
import API_URL from '../../Services/API/APIs';
import { useState, useEffect } from 'react';
import { useParams } from "react-router-dom";
import "./BlogDetail.css";
import { useNavigate } from "react-router-dom";
import { ParseDateTime } from '../../Utils/Date';
import {Helmet} from "react-helmet";



const BlogDetail = () => {
  const { slug } = useParams();
  const [post, setPosts] = useState({});
  const navigate = useNavigate();
  // let params = {'slug': 'what-is-machine-learning'}

   useEffect(() => {
      fetch(`${API_URL}/blogs/detail?slug=${slug}`, {method: "GET"})
         .then((response) => response.json())
         .then((data) => {
            setPosts(data.data);
         })
         .catch((err) => {
            navigate('/*');
         });
   }, []);


    return (
      <>
      {/* adding meta tags below in helmet */}
      <Helmet>
         <meta charSet="utf-8" />
         <title>{post.meta_title}</title>
         <meta name="description" content={post.meta_description} />
         <meta name="keywords" content={post.meta_keywords} />
      </Helmet>
      <section>
      <div className='main-container'>
         <div className='row blog-detail'>
            <div className='col-lg-9'>
               <div className='blog-category-name'>
               {/* <spa>Category > </spa> */}
               {/* {post.category && post.category.map((c) => (<span onClick={()=> navigate(`/category/${c.slug}`)} key={c.id}>{c.name}</span>))} */}
               {post.category && post.category.map((c) => (<span key={c.id}>{c.name}</span>))}

               </div>
               <h1 className='blog-title'>{post.title}</h1>
               <div className='published-on'>
               <span>{ParseDateTime(post.created_on)}</span>  
               {post.author? (<span> | {post.author.name}</span>) : (<></>) }
               </div>
               <img alt='{post.title}' className='title-banner' src={post.thumbnail } />    
               <div className='blogContent' dangerouslySetInnerHTML={{__html: `${post.contents}`}} />
         </div> 
         <div className='col-lg-3 advertisement'>
            <p>Advertisement</p>
         </div>
       </div>
      </div>
      </section>
      <section>
         <div className='main-container'>
            <div className='comment-section col-lg-9'>
               <p>Comment section</p>
            </div>

         </div>
      </section>
      </>
    )
  }


export default BlogDetail
