import React from "react";
import { useState, useEffect } from "react";
import API_URL from "../../Services/API/APIs";
import { Link } from "react-router-dom";
import "./BlogPage.css";
import {Helmet} from "react-helmet";

const Blog = () => {
  const [posts, setPosts] = useState([]);
  useEffect(() => {
    fetch(API_URL + "/blogs/", { method: "GET" })
      .then((response) => response.json())
      .then((data) => {
        console.log(data.results.data);
        setPosts(data.results.data);
      })
      .catch((err) => {
        console.log(err.message);
      });
  }, []);
  // let navigate = useNavigate();

  // function onClickSlug(slug) {
  //   let path = slug;
  //   navigate(path);
  //   console.log("this is the click", slug);
  // }

  return (
    <>
    <Helmet>
         <meta charSet="utf-8" />
         <title>Blog | The Teknology</title>
         <meta name="description" content="Read all blogs related to technology on our website." />
         <meta name="keywords" content="Technology blog, Teknology blog, blogs regarding technology" />
    </Helmet>
      <div className="main-container">
        <div className="row">
        <div className="blogPage col-lg-9">
          {posts.map((el) => (
            <article key={el.id}>
              <div className="row">
                  <div className="leftBlog col">
                    
                      <a href={`/blogs/${el.slug}`}>
                        <img alt="{el.title}" src={el.thumbnail} />
                      </a>
                  </div>
                  <div className="rightHome col">
                    <Link to={`/blogs/${el.slug}`}>
                      <h1>{el.title}</h1>
                    </Link>
                    <p>{el.meta_description}</p>
                  </div>
                </div>
            </article>
          ))}
        </div>
        <div className="col-lg-3">
          <p>Advertisement</p>
        </div>
        </div>

      </div>
    </>
  );
};

export default Blog;
