import React from "react";
import { useState, useEffect } from "react";
import API_URL from "../../Services/API/APIs";
// import { useNavigate } from "react-router-dom";
import { Link } from "react-router-dom";

const FeaturedBlog = () => {
  const [posts, setPosts] = useState([]);
  useEffect(() => {
    fetch(`${API_URL}/blogs/?featured=True&limit=10`, { method: "GET" })
      .then((response) => response.json())
      .then((data) => {
        // console.log(data.results.data);
        setPosts(data.results.data);
      })
      .catch((err) => {
        // console.log(err.message);
      });
  }, []);
  // let navigate = useNavigate();

  // function onClickSlug(slug) {
  //   let path = slug;
  //   navigate(path);
  //   console.log("this is the click", slug);
  // }

  return (
    <>
      {/* {posts.map((post)=> {
         return (
          // <h1 onClick={() => onClickSlug(post.slug)} >{post.title}</h1>
          <Link to={`/blogs/${post.id}`}>
            <h1>{post.title}</h1>
            )
         
        })} */}
      <div className="container mb-5">
        {posts.map((el) => (
          <article key={el.id}>
            <div className="row mb-3 ">
              <div className="col-lg">
                <div className="leftHome">
                  <div className="featureThumbnail">
                    <a href={`/blogs/${el.slug}`}>
                      <img alt="{el.title}" src={el.thumbnail} />
                    </a>
                  </div>
                </div>
                <div className="rightHome">
                  <Link to={`/blogs/${el.slug}`}>
                    <h1>{el.title}</h1>
                  </Link>
                  <p>{el.meta_description}</p>
                </div>
              
              </div>
            </div>
          </article>
        ))}
      </div>
    </>
  );
};

export default FeaturedBlog;
